@import '../../App.scss';
.cart-detail-container {
    display: flex;
    align-items: flex-start;
    margin: 5px 0px;
    padding: 10px;
    background-color: white;
    img {
        width: 5rem;
        height: 5rem;
    }
}

.cart-details-section {
    margin-left: 20px;
    width: 90%;
}

input {
    width: 30px;
    border: none;
}

.cart-details-disp-flex {
    display: flex;
    justify-content: space-between;
    align-items: center;
    span {
        margin-left: 10px;
    }
}

.quantity {
    display: flex;
    align-items: center;
    font-size: 14px;
    input {
        text-align: center;
    }
    p{
        margin-left: 10px;
    }
}

.cart-image-section {
    width: 30%;
}

.circle-button {
    border-radius: 30px;
    width: 30px;
}

.cheap-image-container {
    margin: 10px;
    background-color: white;
    display: flex;
    align-items: center;
    font-size: 14px;
    padding: 5px;
    img {
        margin-right: 20px;
    }
}